import { useState, useEffect } from 'react';
import { List, Typography, Button, Divider } from 'antd';
import { Link } from 'react-router-dom';
import { SearchOutlined } from '@ant-design/icons';

import * as Services from '../services';
import JobItems from '../components/JobItem';

const { Title, Text } = Typography;

export default function Home(){
  useEffect(() => {
    document.title = "Fakejobs - Cek Lowongan Palsu";
  }, []);

  return (
    <div>
      <div className="topContentHome">
        <div>
          <Title>Database Lowongan Palsu</Title>
          <Text style={{ fontSize: '1.3em' }} type="secondary">
            Lowongan Palsu merupakan hal yang merugikan. 
            Laporkan lowongan tersebut di situs ini agar Jobseeker lain tidak tertipu.
          </Text>
        </div>
        <div className="topContentHomeAction">
          <Link to="/report" className="btnPrimary">Laporkan Lowongan Mencurigakan</Link>
          <Link to="/search"><Button icon={<SearchOutlined />} className="btnLink" type="link" size="large">Cek Lowongan Palsu</Button></Link>
        </div>
      </div>
      <Divider orientation="left">
        <Title level={2}>
          Laporan Terbaru
        </Title>
      </Divider>
      <NewFakeJobs />
    </div>
  );
}

function NewFakeJobs() {
  const [loading, setLoading] = useState(true);
  const [fakeJobs, setFakeJobs] = useState([]);

  useEffect(() => {
    Services.getFakeJobsData().then(data => {
      setFakeJobs(data);
      setLoading(false);
    });
  }, []);
  return (
      <List
        loading={loading}
        className="container"
        itemLayout="horizontal"
        dataSource={fakeJobs}
        renderItem={item => (
          <List.Item>
            <JobItems item={item}/>
          </List.Item>
        )}
      />
  )
}