import { Comment, Tooltip, Avatar, Typography, Button } from 'antd';
import { CheckCircleOutlined, ClockCircleOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';
import moment from 'moment';

const { Title } = Typography;

export default function JobItems({ item }) {
  return (
    <Comment
      className="jobItem"
      author={
        <Link to={'/job/'+item.id}>
          <Title level={4} style={{ color: '#fff' }}>
            Lowongan {item.job_title}</Title>
        </Link>
      }
      avatar={
        <Avatar
          src={<CheckCircleOutlined/>}
          alt="Ico"
        />
      }
      content={
        <div className="fakeJobDescription">
          <p>{truncateString(item.job_description, 60)}</p>
          <Button style={{ marginTop: 10 }}>
            <Link to={'/job/'+item.id}>
            Read More
            </Link>
          </Button>
        </div>
      }
      datetime={
        <Tooltip title={moment(item.published_at).format('YYYY-MM-DD HH:mm:ss')}>
          <ClockCircleOutlined style={{ marginRight: 5 }} />
          <span>{moment(item.published_at).fromNow()}</span>
        </Tooltip>
      }
    />
  )
}

function truncateString(str, no_words) {
  return str.split(" ").splice(0,no_words).join(" ");
}
