import axios from 'axios';
import { BASE_URL } from '../configs/env';
import API from '../configs/api';

const api = axios.create({
  baseURL: BASE_URL,
});

export function getFakeJobsData(){
  return new Promise((resolve, reject) => {
    api.get(API.GET_NEW_FAKE_JOBS).then(res => {
      resolve(res.data);
    }).catch(err => {
      reject(err);
    });
  });
}

export function getFakeJobData(id){
  return new Promise((resolve, reject) => {
    api.get(API.GET_FAKE_JOB.replace(/{(id)}/, id)).then(res => {
      resolve(res.data);
    }).catch(err => {
      reject(err);
    });
  });
}

export function getSearchFakeJobByEmail(email){
  return new Promise((resolve, reject) => {
    api.get(API.GET_SEARCH_FAKE_JOBS_BY_EMAIL.replace(/{(email)}/, email)).then(res => {
      resolve(res.data);
    }).catch(err => {
      reject(err);
    });
  });
}

export function postFakeJobsData(data) {
  return new Promise((resolve, reject) => {
    api.post(API.POST_FAKE_JOBS, data).then(res => {
      resolve(res.data);
    }).catch(err => {
      reject(err);
    });
  });
}